.main-notes {
    margin: auto;
    position: relative;
    width: 100%;

    @media (min-width: $mobile_size) {
        width: 70%;
    }


}

.typing {
    margin: 20vh auto 0;
    display: flex;

    div {
        text-align: center;
        font-family: logo;
        font-size: 2.8em;

    }

    img {
        margin-left: 20px;
        width: 60px;
        height: 60px;
        align-self: flex-end;
    }

}